function showProduct(){
  document.getElementById('productButton').addEventListener('click', myFunction);
  function myFunction(){
    var main = document.getElementById('graphic');
    var product = document.getElementById('product');
    var footer = document.getElementById('footer');
    var service = document.getElementById('service');
    if (main.style.display == 'flex' || service.style.display == 'flex'){
      main.style.display = 'none';
      footer.style.display = 'none';
      service.style.display = 'none';
      product.style.display = 'flex';
    }
    else {
      main.style.display = 'flex';
      footer.style.display = 'flex';
      product.style.display = 'none';
      service.style.display = 'none';
    }
  }
}
addLoadEvent(showProduct);

function showService(){
  document.getElementById('serviceButton').addEventListener('click', myFunction);
  function myFunction(){
    var main = document.getElementById('graphic');
    var service = document.getElementById('service');
    var footer = document.getElementById('footer');
    var product = document.getElementById('product');
    if (main.style.display == 'flex' || product.style.display == 'flex'){
      main.style.display = 'none';
      footer.style.display = 'none';
      product.style.display = 'none';
      service.style.display = 'flex';
    }
    else {
      main.style.display = 'flex';
      footer.style.display = 'flex';
      service.style.display = 'none';
      product.style.display = 'none';
    }
  }
}
addLoadEvent(showService);



